.modal {
  position: fixed;
  z-index: 500;
  background-color: white;
  width: 500px;
  max-height: 80%;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 16px;
  left: 20%;
  top: 20%;
  overflow: auto;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
}

@media (min-width: 600px) {
  .modal {
    left: calc(40%);
  }
}
